import "./Card.styles.css";
const Card = (props) => {
  const selectIndex = () => {
    props.selectIndex(props.index);
  };
  return (
    <div className="card-container" onClick={selectIndex}>
      <h5 className="country">{props.user.location.country}</h5>
      <img src={props.user.picture.large} alt="img" />
      <h4>{`${props.user.name.title} ${props.user.name.first} ${props.user.name.last}`}</h4>
    </div>
  );
};

export default Card;
