import "./SearchBox.css";
const SearchBox = (props) => {
  return (
    <div className="allFilter">
      <h2>{props.counter}</h2>
      <div className="filter-container">
        <input
          className="SearchBox"
          type="search"
          placeholder="search users"
          onChange={props.change}
        />
        <select className="genderSelect" onChange={props.changeGender}>
          <option value="all" key="00">
            All
          </option>
          {props.gender.map((g, i) => (
            <option value={g} key={i}>
              {g}
            </option>
          ))}
        </select>

        <select className="countrySelect" onChange={props.changeCountry}>
          <option value="all" key="00">
            All
          </option>
          {props.country.map((c, i) => (
            <option value={c} key={i}>
              {c}
            </option>
          ))}
        </select>
      </div>
    </div>
  );
};

export default SearchBox;
