import react, { Component } from "react";
import CardList from "./components/CardList/CardList";
import SearchBox from "./components/SearchBox/SearchBox";
import Overlay from "./components/OverLay/Overlay";
import Popup from "./components/Popup/Popup";
import "./App.css";

class App extends Component {
  constructor() {
    super();
    this.state = {
      users: [],
      searchText: "",
      selectID: "",
      isShow: false,
      gender: "all",
      country: "all",
    };
  }
  componentDidMount() {
    fetch("https://randomuser.me/api/?results=50")
      .then((response) => response.json())
      .then((users) => this.setState({ users: users.results }));
  }

  changeWithSearch = (e) => {
    this.setState({ searchText: e.target.value });
  };
  changeGender = (e) => {
    this.setState({ gender: e.target.value });
  };
  changeCountry = (e) => {
    this.setState({ country: e.target.value });
  };
  changeShow = () => {
    this.setState((prevState, prevProps) => ({ isShow: !prevState }));
  };
  selectByIndex = (newIndex) => {
    this.setState({ selectID: newIndex });
    this.setState({ isShow: true });
  };

  render() {
    const { users, searchText, isShow, selectID, gender, country } = this.state;

    const fillterUsers = users.filter((user) =>
      gender !== "all" && country !== "all"
        ? user.name.first.toLowerCase().includes(searchText.toLowerCase()) &&
          user.gender === gender &&
          user.location.country === country
        : gender !== "all" && country === "all"
        ? user.name.first.toLowerCase().includes(searchText.toLowerCase()) &&
          user.gender === gender
        : gender === "all" && country !== "all"
        ? user.name.first.toLowerCase().includes(searchText.toLowerCase()) &&
          user.location.country === country
        : user.name.first.toLowerCase().includes(searchText.toLowerCase())
    );

    const genders = [...new Set(users.map((user) => user.gender))];

    const countries = [...new Set(users.map((user) => user.location.country))];
    return (
      <div className="App">
        {isShow ? <Overlay change={this.changeShow} /> : null}
        {isShow ? <Popup user={fillterUsers[selectID]} /> : null}
        <h1>welcome to my first react project</h1>
        <SearchBox
          counter={fillterUsers.length}
          change={this.changeWithSearch}
          gender={genders}
          changeGender={this.changeGender}
          country={countries}
          changeCountry={this.changeCountry}
        />
        <CardList users={fillterUsers} selectIndex={this.selectByIndex} />
      </div>
    );
  }
}
export default App;
