import Card from "../Card/Card";
import "./CardList.css";
const CardList = (props) => {
  return (
    <div className="cards-container">
      {props.users.map((user, index) => (
        <Card key={index} user={user} selectIndex={props.selectIndex} index={index} />
      ))}
    </div>
  );
};

export default CardList;
